import { useEffect, useState } from 'react';

export default function Timer({ duration }) {
  const [time, setTime] = useState(duration);

  useEffect(() => {
    setTimeout(() => {
      setTime(time - 1000);
    }, 1000)
  }, [time]);

  const formattedTime = (miliseconds) => {
    let daystring = 'Days';
    let hrstring = 'Hours';
    let minstring = 'Minutes';
    let secstring = 'Seconds';
    let seconds = parseInt(Math.floor(miliseconds / 1000));
    let min = parseInt(Math.floor(seconds / 60));
    let hrs = parseInt(Math.floor(min / 60));
    let days = parseInt(Math.floor(hrs / 24));

    let sec = parseInt(seconds % 60);
    let mins = parseInt(min % 60);
    let hours = parseInt(hrs % 24);

    if (days === 1) daystring = 'Day'
    if (hours === 1) hrstring = 'Hour'
    if (mins === 1) minstring = 'Minute'
    if (sec === 1) secstring = 'Second'
    


    return `${days} ${daystring} : ${hours} : ${hrstring} ${mins} : ${minstring} ${sec} : ${secstring}`;
  }

  return formattedTime(time);
}
