import React from 'react'
import company from '../assets/logo-white.svg';
import './style.css'
import Timer from './timer';
import dayjs from "https://unpkg.com/dayjs@1.11.10/esm/index.js";

export default function Navbar() {
  return (
    <div style={{ background: 'black' }}>
      <nav className="navbar navbar-expand-lg navbar-dark " style={{ background: "black", borderBottom: "1px solid goldenrod" }}>
        <div className="container">
          <a className="nav-link active" aria-current="page" href="https://www.aicerts.io/"  target="_blank">
            <img className="logo" src={company} alt='logo' style={{ height: "35px" }} />
          </a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <a className="nav-link active" href="https://www.aicerts.io/"  target="_blank"><span className='links'>Home</span></a>
              </li>
              <li className="nav-item">
                <a className="nav-link active" href="https://www.aicerts.io/why/" target="_blank"><span className='links'>About</span></a>
              </li>
              <li className="nav-item">
                <a className="nav-link active" href="https://www.aicerts.io/publications/"  target="_blank"><span className='links'>Resources</span></a>
              </li>
              <li className="nav-item">
                <a className="nav-link active" href="https://store.aicerts.io/"  target="_blank"><span className='links'>Store</span></a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      <div className="main">
        <div className="main-content">
          <div className='title'>We are Arriving Soon!</div>
          <div className='title'>{dayjs().format('D MMMM YYYY')}</div>
          <div className="title">{<Timer duration={2 * 24 * 60 * 60 * 1000} />}</div>
        </div>
      </div>
    </div>
  )
}
